import React from "react"

class Resume extends React.Component{
	constructor(){
		super()
		this.state = {
			downloadHover: false
		}
		this.toggleHover = this.toggleHover.bind(this)
	}
	toggleHover(e){
		this.setState((prevState) => {
			console.log(prevState[e])
			return {[e]: !prevState[e]}
		})
	}
	render(){
		return(
			<div className="resume">
				<h2> Resume </h2>
				<div className = "download-resume">
					<a 	href={require("../Assets/Resume.pdf")} 
						target='_blank'
						onMouseEnter={() => {this.toggleHover("downloadHover")}}
						onMouseLeave={() => {this.toggleHover("downloadHover")}}
						style={{backgroundColor: (this.state.downloadHover? "lightgrey" : "white")}}>
						{this.state.downloadHover?
						<img src = {require("../Assets/pdf_icon.png")} />
						:<h3>Resume View / Download</h3>
						}
					</a>
				</div>
				<ul>
					<h3 className="resume-header">Education</h3>
					<ul>
						<li>San Francisco State University</li>
						<ul>
							<li>Bachelor of Science in Computer Science</li>
							<li>3.69 GPA, 8 semesters on dean's list </li>
						</ul>
					</ul>
					<ul>
						<li>The Urban School of San Francisco </li>
					</ul>
					<h3 className="resume-header">Skills</h3>
					<ul>
						<li>Programming languages and frameworks with the most experience:</li>
						<ul>
							<li className = "skills-list"> 
								<img src={require("../Assets/nodeIcon.png")} />
								<p> NodeJS - 2018-current</p>
							</li>
							<li className = "skills-list"> 
								<img src={require("../Assets/reactIcon.png")} />
								<p> ReactJS - 2018-current</p>
							</li>
							<li className = "skills-list"> 
								<img src={require("../Assets/dbIcon.png")} />
								<p> SQL - 2016-current</p>
							</li>
							<li className = "skills-list"> 
								<img src={require("../Assets/phpIcon.png")} />
								<p> PHP - 2015-current</p>
							</li>
						</ul>
						<li>Languages and concepts I have experience in:</li>
						<ul>
							<li>RESTful API creation: (features I have written)</li>
							<ul>
								<li>
									Full Custom Database using ORM, password encryption, web token authentication, secure email verification / password reset with web tokens								
								</li>
							</ul>
							<li>Database management, Amazon Web Service hosting and administration, Cryptography, HTML, CSS, Linux/ Unix, Agile Software development, Ruby, Android development, Unity game development, Prolog, Scheme, MIPS assembly architecture.</li>
						</ul>
						<li>Code samples and previous projects can be found on my <a target="_blank" href=" https://github.com/jkrubin">Github</a></li>
						<li>Always learning new programming languages and concepts</li>
					</ul>
					<h3 className="resume-header">Experience</h3>
					<ul>
						<li>2018-current - Freelance Web Developer</li>
						<ul>
							<li>'Top Rated' Developer</li>
							<li> In my time as a freelance web developer, I have worked on several projects, ranging from custom plugins and general development for a wordpress website, to a custom polling software written in NodeJS</li>
							<li> Backend Engineer for stock analytics startup: Wrote from scratch and maintain a custum DB/API to track, analyze, and compare a growing database of stock symbols, using IEX and EOD APIs.</li>
							<li>Fullstack engineer creating microservices: Worked on a large codebase of a startup creating various microservices utilizing AirTable, Jotform, as well as several other services. At this job I worked with full React.js/Redux/TypeScript front end, to implement multiple new features, and Node/Express/Knex backend to create the supporting APIs, as well as debugging/improving old features across the stack.</li>
							<li> Fullstack Engineer for medical wellness startup: Created custom DB/API to track medical wellness data, as well as front end work in react/redux, implementing new features and bug fixing. </li>
							<li> Backend Engineer creating payment managing software using Docusign and Twilio APIs for contract creation and mailing, as well as Stripe APIs for payment collection</li>
							<li> Development with: Wordpress, PHP, SQL, NodeJS, AWS</li>
						</ul>
						<li>Group Work Experience</li>
						<ul>
							<li>Backend PHP developer for a 5 person team creating an apartment listing website from scratch.</li>
							<li>Backend Java developer for a 7 person Unity game dev team</li>
							<li>Part of a 10 person team who designed a new unofficial Android mobile application for SFSU</li>
						</ul>
						<li>Backend development for multiple startups, including the responsibilities: </li>
						<ul>
							<li>Node.js RESTful api development</li>
							<li>Database design and creation</li>
							<li>Amazon aws services administrator</li>
						</ul>
					</ul>
				</ul>
			</div>
		)
	}
}
export default Resume