import React from "react"
import ParalaxBackground from "./ParalaxBackground"
import ParalaxSlider from "./ParalaxSlider"
import Resume from "./Resume"
import Projects from "./Projects"
import ScrollableAnchor from 'react-scrollable-anchor'

class HomePage extends React.Component{
	constructor(){
		super()
		this.state = {
			links: [],
			hovered: false,
			openMenu: false
		}
		this.setLinks = this.setLinks.bind(this)
		this.toggleMenu = this.toggleMenu.bind(this)
		this.closeMenu = this.closeMenu.bind(this)
	}
	setLinks(newLinks){
		if(this.state.links.length === 0){
			this.setState({links: newLinks})
		}
	}
	toggleMenu(){
		this.setState((prevState) =>{
			return {openMenu: !prevState.openMenu}
		})
	}
	closeMenu(){
		this.setState({openMenu: false})
	}
	render(){
		let linkDisplay = this.state.links.map((link) =>{
			return(
				<li className="anchor-link">
					<a href={link.ref}> {link.name} </a>
				</li>
			)
		})
		let dropdownDisplay = this.state.links.map((link) =>{
			return(
				<div onClick={this.closeMenu} className={`hamburger-link inner`} onMouseEnter={()=>{this.setState({hovered: link.ref})}} onMouseLeave={()=>{this.setState({hovered:false})}}>
					<a href = {link.ref}>
						<a><span className="dd-link-text">{link.name}</span></a>
					</a>
				</div>
			)
		})
		return(
			<>
				<div className="dropdown-menu" style={this.state.openMenu? {maxWidth: '1000px'} : {maxWidth: '0px'}}>
						<div className={this.state.openMenu? "dd-menu open-menu" : "dd-menu"}>
							<img src={this.state.openMenu? require('../Assets/menuX.png') : require('../Assets/hamburger.png')} 
							onClick={this.toggleMenu}
							style={this.state.openMenu ? {transform: 'rotate(90deg)'} : {}}/>	
						</div>
					<div className="dropdown-inner">
						<div onClick={this.closeMenu} className="hamburger-link">
							<a href='#resume'> Resume </a>
						</div>
						<div onClick={this.closeMenu} className="hamburger-link">
							<a href='#projects'> Projects </a>
						</div>
						<div>
							{dropdownDisplay}
						</div>
					</div>								
				</div>				
				<div className={`homepage ${this.state.openMenu? 'menu-open': ''}`}>
					<ParalaxSlider />
					<div className="content-container">
						<div className="content">
							<div className={this.state.openMenu? "anchor-menu open-menu" : "anchor-menu"}>
								<img src={this.state.openMenu? require('../Assets/menuX.png') : require('../Assets/hamburger.png')} 
								onClick={this.toggleMenu}
								style={this.state.openMenu ? {transform: 'rotate(90deg)'} : {}}/>	
							</div>
							<div className="profile">
								<div className="prof-pic-container">
									<img src={require('../Assets/portrait.jpeg')} className="prof-pic" />
								</div>
								<h1 id="name">Joshua Rubin</h1>
								<div className="sidebar">
									<div className="about">
										<h3>About Me</h3>
										<p>
											I love solving puzzles and using creativity to think of new solutions to difficult problems. As a developer, I love applying my creativity to a team to create awesome projects. I am a full-stack Software engineer from San Francisco, specializing in backend development, creating database schema and RESTful APIs in NodeJS. But also very comfortable getting my hands dirty in react/redux  
										</p>
									</div>
									<div className="sticky-sidebar">
										<ul>
											<li className="anchor-link">
												<a href='#resume'> Resume </a>
											</li>
											<li className="anchor-link">
												<a href='#projects'> Projects </a>
											</li>
											<ul>
												{linkDisplay}
											</ul>
										</ul>
									</div>
								</div>
							</div>
							<div className="main">
								<ScrollableAnchor id ='resume'>
									<Resume />
								</ScrollableAnchor>
								<ScrollableAnchor id ='projects'>
									<Projects setLinks = {this.setLinks}/>
								</ScrollableAnchor>
							</div>
						</div>
					</div>
				</div>
			</>
		)
	}
}

export default HomePage